body,
div,
p,
ul,
ol,
h1 {
    margin: 0;
}

ul {
    padding: 0;
}

body {
    font-family: 'Kalam', cursive;
}

.wrap {
    padding-top: 50px;
    width: 100%;
    height: 100vh;
    background: #f5f5f5;
}

.title {
    font-size: 80px;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
}

.todo {
    width: fit-content;
    margin: 0 auto;
    position: relative;
    box-shadow: 0px 5px 11px -3px rgba(0,0,0,0.75);
}

.todo::after,
.todo::before {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 5px;
    background: #f9f9fb;
    border: 1px solid #ebe9e9;
    box-shadow: -1px 0px 2px 0px rgba(0,0,0,0.45);
}

.todo::after {
    top: calc(100% + 5px);
    width: 96%;
}

.todo::before {
    top: 100%;
    z-index: 1;
    width: 98%;
    border-top: 0;
}

.todo__form {
    display: flex;
    height: 45px;
}

.todo__btn,
.todo__input {
    font-size: 24px;
    font-family: inherit;
    border: 0;
    outline: none;
    background:#fff;
}

.todo__input {
    padding: 1px 50px;
    border-top-left-radius: 6px;   
    box-shadow: inset 2px 0px 5px -4px rgba(0,0,0,0.75);
}

.todo__btn {
    padding: 1px 10px;
    border-top-right-radius: 6px;
    border-left: 1px solid #ededed;
    box-shadow: inset 0px 0px 6px -4px rgba(0,0,0,0.75);
    cursor: pointer;
    transition: background .3s;
}

.todo__btn:hover {
    background: #faf9f9;
}

.todo__list {
    background:#fff;
}

.todo__task {
    display: flex;
    border-top: 1px solid #ededed;
    font-weight: 100;
    line-height: 2em;
    font-size: 1.5em;
    box-shadow: inset 0px 0px 5px -4px rgba(0,0,0,0.75);
}

/* .todo__task-check:checked + .todo__task-label {
    text-decoration: line-through;
    color: #e2dede;
} */

.todo__task-label_checked {
    text-decoration: line-through;
    color: #e2dede;
}

.todo__task-label_checked::after {
    background: url('./img/check-mark.svg') no-repeat center;
    background-size: 65%;
}

/* .todo__task-check:checked + .todo__task-label::after {
    background: url('img/check-mark.svg') no-repeat center;
    background-size: 65%;
} */

.todo__task-label {
    display: block;
    width: 80%;
    position: relative;
    padding-left: 70px;
    cursor: pointer;
}

.todo__task-check {
    visibility: hidden;
    position: absolute;
}

.todo__task-label::after {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-55%);
    border: 1px solid #e2dede;
    border-radius: 50%;
}

.todo__footer {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    border-top: 1px solid #ededed;
    font-weight: 100;
    line-height: 2.5em;
    font-size: 1.15em;
    border-bottom: 1px solid #ededed;
    position: relative;
    z-index: 2;
    box-shadow: 0px 1px 3px -1px rgba(0,0,0,0.45);
    background: #fff;
}

.todo__taks-amount {
    margin-left: 15px;
}

.todo__switch {
    background: #fff;
    border: 0;
    padding: 0 9px;
    line-height: 2em;
    border-radius: 5px;
    outline: none;
}

.todo__switch-active {
    border: 1px solid rgba(175, 47, 47, 0.2);
}

.todo__img {
    width: 15px;
    height: 15px;
}

.todo__delet {
    padding: 0 16px;
    background: 0;
    border: 0;
    outline: 0;
    cursor: pointer;
}

.todo__delet:hover .todo__img {
    fill: #54b7a5;
}
